:root {
  /* FONTS */
  --basci-font: "worksans", "Arial", sans-serif;
  /* COLORS */
  --bg: #FFFFFF;
  --radial-bg: radial-gradient(circle, rgb(255 255 255 / 100%) 0%, rgb(203 203 203 / 100%) 100%);
  --text-primary: #141414;
  --text-secondary: #666666;
  /* PADDINGS */
  --standart-side-padding: 20px;
  --standart-top-padding: 80px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--basci-font);
}

@font-face {
  src: url("../fonts/worksans.woff2") format("woff2"), url("../fonts/worksans.woff") format("woff");
  font-family: worksans;
  font-display: swap;
}
.container {
  margin: 0 auto;
  max-width: 1200px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

:root {
  /* FONTS */
  --basci-font: "worksans", "Arial", sans-serif;
  /* COLORS */
  --bg: #FFFFFF;
  --radial-bg: radial-gradient(circle, rgb(255 255 255 / 100%) 0%, rgb(203 203 203 / 100%) 100%);
  --text-primary: #141414;
  --text-secondary: #666666;
  /* PADDINGS */
  --standart-side-padding: 20px;
  --standart-top-padding: 80px;
}

.body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr max-content;
  min-height: 100vh;
  background: var(--radial-bg);
}

.main {
  padding: var(--standart-top-padding) var(--standart-side-padding);
  width: 100%;
  height: 100%;
}

.main__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

/* BASE */
.footer {
  padding: var(--standart-top-padding) var(--standart-side-padding);
  width: 100%;
  height: max-content;
  box-shadow: 0 -2px 5px rgba(156, 156, 156, 0.2);
  background-color: transparent;
}

.footer__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__text {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  letter-spacing: 0.8px;
  text-decoration: none;
  color: var(--text-secondary);
}

.footer__link:hover {
  color: var(--text-primary);
}

/* DESCTOPE */
:root {
  /* FONTS */
  --basci-font: "worksans", "Arial", sans-serif;
  /* COLORS */
  --bg: #FFFFFF;
  --radial-bg: radial-gradient(circle, rgb(255 255 255 / 100%) 0%, rgb(203 203 203 / 100%) 100%);
  --text-primary: #141414;
  --text-secondary: #666666;
  /* PADDINGS */
  --standart-side-padding: 20px;
  --standart-top-padding: 80px;
}

@media (width > 900px) {
  .footer__adress {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }
  .footer__text:not(:last-child)::after {
    content: "|";
    position: absolute;
    right: -10px;
    top: 0;
    width: 2px;
    height: 100%;
    transform: translateX(1px);
  }
}
/* TABLET */
:root {
  /* FONTS */
  --basci-font: "worksans", "Arial", sans-serif;
  /* COLORS */
  --bg: #FFFFFF;
  --radial-bg: radial-gradient(circle, rgb(255 255 255 / 100%) 0%, rgb(203 203 203 / 100%) 100%);
  --text-primary: #141414;
  --text-secondary: #666666;
  /* PADDINGS */
  --standart-side-padding: 20px;
  --standart-top-padding: 80px;
}

@media (width > 700px) and (width <= 900px) {
  .footer__adress {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: max-content max-content;
    gap: 20px;
  }
  .footer__adress .footer__text:nth-child(odd) {
    justify-self: flex-end;
  }
  .footer__adress .footer__text:nth-child(even) {
    justify-self: flex-start;
  }
}
/* MOBILE */
